import axios from 'axios'
import Vue from 'vue'
import Message from '@/utils/message'
// import store from '../store'
import { getToken } from '@/utils/auth'

// 创建axios实例
const service = axios.create({
  baseURL: process.env.BASE_API, // api的base_url
  timeout: 15000 // 请求超时时间
})

// request拦截器
service.interceptors.request.use(config => {
	// 加header
  if (getToken()) {
    // config.headers['session'] = getToken() // 让每个请求携带自定义token 请根据实际情况自行修改
  } else {
    console.log('goto miniProgram')
    Vue.wechat.miniProgram.navigateTo({
      url: '/pages/activities/activityWebView?type=newgift'
    })
  }
  return config
}, error => {
  // Do something with request error
  console.log(error) // for debug
  Promise.reject(error)
})

// respone拦截器
service.interceptors.response.use(
  response => {
  /**
  * code为非20000是抛错 可结合自己业务进行修改
  */
    const res = response.data
    // if (res.code !== 0) {
    const isDownload = response.headers['content-type'] === 'application/octet-stream;charset=UTF-8'
    // const isDownload = 'application/octet-stream'.indexOf(response.headers['content-type']) !== -1
    if (res.code !== 0 && !isDownload && res.errorcode!=0) {
			Message.toast(res.errormsg || res.data || '返回值code异常')
      // 登陆问题
      const errList = [
        { code: 1013, msg: 'IP异常', relogin: true },
        { code: 10002, msg: '请登录后再操作，谢谢！', relogin: true },
        { code: 1015, msg: '会话过期！请刷新浏览器再次登录!', relogin: true },
        { code: 1001, msg: '验证码错误' },
        { code: 1000, msg: '参数错误' },
        { code: 500, msg: '服务器内部错误' },
        { code: 1012, msg: '无操作权限验证', relogin: true }
      ]
      // 50008:非法的token; 50012:其他客户端登录了;  50014:Token 过期了;
      for (const error of errList) {
        if (res.errorcode === error.code) {
          if (error.relogin) { // 需要刷新页面
            // MessageBox.confirm('你已被登出，可以取消继续留在该页面，或者重新登录', '确定登出', {
            //   confirmButtonText: '重新登录',
            //   cancelButtonText: '取消',
            //   type: 'warning'
            // }).then(() => {
            //   store.dispatch('FedLogOut').then(() => {
            //     location.reload()// 为了重新实例化vue-router对象 避免bug
            //   })
            // })
          } else {
						Message.toast(res.errormsg || error.msg)
          }
        }
      }
      // return Promise.reject('error')
      return Promise.reject(res)
    } else {
      return response.data
    }
  },
  error => {
		console.log('err' + error)// for debug
		Message.toast(error.message || error.msg)
    return Promise.reject(error)
  }
)

export default service



