/**
*
* @api {method} @/utils/window.js 自定义窗口 弹出框&toast
* @apiName apiName
* @apiGroup window
* @apiVersion  v1.0
*/

import Vue from 'vue'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

Vue.use(ToastPlugin)
Vue.use(LoadingPlugin)

/**
* 显示模态alert
*
* @param {any} title
*/
function alert (title) {
Vue.$vux.loading.show(title)
}

function hideAlert() {
Vue.$vux.loading.hide()
}

/**
* 显示toast
*
* @param {string} [title='加载中...']
* @param {string} [position='top']
*/
function toast (title = '加载中...', position = 'top') {
Vue.$vux.toast.text(title, position)
}

export default {
alert,
hideAlert,
toast
}


